'use client';
import { motion, AnimatePresence } from 'framer-motion';

interface AlertModalProps {
    isOpen: boolean;
    onClose: () => void;
    onAction?: () => void;
    title: string;
    message: string;
    type?: 'info' | 'success' | 'error' | 'warning';
    actionLabel?: string;
    cancelLabel?: string;
}

const AlertModal = ({
    isOpen,
    onClose,
    onAction,
    title,
    message,
    type = 'info',
    actionLabel = 'Acknowledge',
    cancelLabel = 'Cancel'
}: AlertModalProps) => {
    // Determine colors based on type
    const getTheme = () => {
        switch (type) {
            case 'success':
                return { border: 'border-accent-moss/30', bg: 'bg-accent-moss/10', text: 'text-accent-moss', btn: 'bg-accent-moss text-white hover:bg-clinical-ink' };
            case 'error':
                return { border: 'border-signal-error/30', bg: 'bg-signal-error/10', text: 'text-signal-error', btn: 'bg-signal-error text-white hover:bg-red-900' };
            case 'warning':
                return { border: 'border-accent-bronze/30', bg: 'bg-accent-bronze/10', text: 'text-accent-bronze', btn: 'bg-accent-bronze text-white hover:bg-clinical-ink' };
            default: // info
                return { border: 'border-clinical-ink/10', bg: 'bg-clinical-ink/5', text: 'text-clinical-ink', btn: 'bg-clinical-ink text-white hover:bg-black' };
        }
    };

    const theme = getTheme();

    return (
        <AnimatePresence>
            {isOpen && (
                <>
                    {/* Backdrop */}
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onClose}
                        className="fixed inset-0 bg-clinical-ink/40 backdrop-blur-md z-[60] transition-colors"
                    />

                    {/* Modal */}
                    <div className="fixed inset-0 flex items-center justify-center p-4 z-[60] pointer-events-none">
                        <motion.div
                            initial={{ scale: 0.95, opacity: 0, y: 10 }}
                            animate={{ scale: 1, opacity: 1, y: 0 }}
                            exit={{ scale: 0.95, opacity: 0, y: 10 }}
                            className="bg-clinical-canvas border border-clinical-ink/10 shadow-2xl rounded-3xl p-8 md:p-12 w-full max-w-md pointer-events-auto relative overflow-hidden"
                        >
                            {/* Decorative Elements */}
                            <div className="absolute top-0 right-0 p-6 opacity-5 pointer-events-none">
                                <svg width="64" height="64" viewBox="0 0 64 64" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <circle cx="32" cy="32" r="31" stroke="currentColor" strokeWidth="1" />
                                </svg>
                            </div>

                            <div className={`inline-flex items-center gap-2 mb-6 px-3 py-1 rounded-full border ${theme.border} ${theme.bg}`}>
                                <span className={`font-mono text-[10px] uppercase tracking-widest ${theme.text}`}>
                                    {type}
                                </span>
                            </div>

                            <h3 className="font-display font-black text-2xl md:text-3xl text-clinical-ink mb-4 uppercase leading-[0.9]">
                                {title}
                            </h3>
                            <p className="font-sans text-sm text-clinical-ink/60 mb-10 leading-relaxed">
                                {message}
                            </p>

                            <div className="flex gap-4">
                                {onAction && (
                                    <button
                                        onClick={onClose}
                                        className="flex-1 py-4 bg-transparent border border-clinical-ink/20 text-clinical-ink hover:bg-clinical-ink/5 font-sans text-[10px] uppercase tracking-[0.2em] font-bold transition-all duration-300 rounded-full"
                                    >
                                        {cancelLabel}
                                    </button>
                                )}
                                <button
                                    onClick={onAction || onClose}
                                    className={`flex-1 py-4 ${theme.btn} font-sans text-[10px] uppercase tracking-[0.2em] font-bold transition-all duration-300 rounded-full shadow-lg`}
                                >
                                    {actionLabel}
                                </button>
                            </div>
                        </motion.div>
                    </div>
                </>
            )}
        </AnimatePresence>
    );
};

export default AlertModal;
